.sidebar {
  display: flex;
  flex-direction: column;
  flex: 0.35;
}
.sidebar__search {
  display: flex;
  align-items: center;
  background-color: #f6f6f6;
  height: 39px;
  padding: 10px;
}
.sidebar__searchContainer {
  display: flex;
  align-items: center;
  background-color: white;
  width: 100%;
  height: 35px;
  border-radius: 20px;
}
.sidebar__searchContainer > input {
  border: none;
  padding: 10px;
  outline-width: 0;
  background-color: transparent;
}

.sidebar__header {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  border-right: 1px solid lightgray;
}
.sidebar__headerRight {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-width: 10vw;
}

.sidebar__headerRight > .MuiSvgIcon-root {
  margin-right: 2vw;
  font-size: 24px !important;
}
.sidebar__chats {
  flex: 1;
  background-color: white;
  overflow: scroll;
}
.sidebar__chats::-webkit-scrollbar {
  display: none;
}
.sidebar__chats > div {
  border-bottom: 1px solid lightgray;
  cursor: pointer;
  padding: 20px;
}
.sidebar__chats > div:hover {
  background-color: #ebebeb;
}
.sidebar__chats > div > .sidebar__chatsInfo {
  display: flex;
  justify-content: space-between;
}
